@charset "utf-8";

/* 공통클래스/웹폰트 CSS - core.css */
/* 웹폰트, 공용클래스 */

/* 전체 글자크기 Root 셋팅! */
html{
    font-size: 16px;
}
body * {
    /* 공사중 표시 */
    /* outline: 1px dashed gray; */
    line-height: 1.5;
    box-sizing: border-box;
}
::selection {
    color: #fff;
    background-color: #fbb;
}
html, a, body, input, label, textarea {
    cursor: none;
}



/******************** 2. 공통 클래스 ********************/

/* 속박스 class */
.ibx{
    max-width: 1100px;
    /* 최대가로크기 1100px이면
    싸고 있는 박스가 이것보다 작아질 경우
    auto로 자동전환되어 가로스크롤이 안생김! */
    margin: 0 auto;
}

