/* float 셋팅 클래스 */
.clb > li{
    float: left;
}
/* float설정 지우기 클래스 */
.clb::after{
    content: '';
    display: block;
    clear: both;
}

/* IR클래스 */
/* 배경이미지 설명클래스 (Image Replacement) */
.ir{
    display: block;
    width: 0;
    height: 0;
    overflow: hidden;
}